
body{
  background: #21518b;
}
.share-icon{
  display:none;
}
#live{
  display: none;
  width:7.5rem;
  height:3.2rem;
  color:#fff;
  margin: 0 auto;
  text-align: center;
  letter-spacing:.01rem;
  background: url(./components/index/images/bg.jpg) no-repeat;
  background-size: 100%;
}
#live h2{
  font-size:.36rem;
  padding-top:.6rem;
}
#live p{
  font-size: .27rem;
  margin:.4rem auto;
}
#live a{
  display: inline-block;
  font-size: .27rem;
  width: 3rem;
  height: .64rem;
  line-height: .64rem;
  background: #3670d1;
  margin: 0 .1rem;
}
#live .status2{
  text-align: left;
  text-indent: .7rem;
  background: url(./components/index/images/icon_play.png) 2.1rem center no-repeat #3670d1;
  background-size: .3rem;
  opacity: .5;
}
#live .status3{
  text-align: left;
  text-indent: .7rem;
  background: url(./components/index/images/icon_play.png) 2.1rem center no-repeat #3670d1;
  background-size: .3rem;
}
#live .hide{
  display: none;
}
#index {
  width: 7.5rem;
  height: 13.36rem;
  margin: 0 auto;
  background: url(./components/index/images/BG_h5.png) no-repeat;
  background-size: 7.5rem 13.66rem;
  position: relative;
}
.clearT{
  overflow:hidden;
}
.ft{
  opacity: 0;
  animation: itemft 2.7s forwards;
  -webkit-animation: itemft 2.7s forwards;
}
.hx-link{
  display: block;
  position: fixed;
  right: .1rem;
  top: 50%;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}
.hx-link img{
  width: .8rem;
  height: 2.28rem;
}
.index-menu{
  padding: .3rem .3rem 0 .3rem;
  overflow: hidden;
}
.index-menu a{
  overflow: hidden;
}
.index-menu .logo img{
  height: .32rem;
  margin-top: .055rem;
  float: left;
}
.index-menu .share img{
  width: .4rem;
  height: .4rem;
  float: right;
}
.tel400{
  text-align: center;
  font-size: .24rem;
  color: #fff;
  line-height: .36rem;
  opacity: .8;
}
.tel400 span{
  margin-right: .05rem;
}
.footer{
  text-align: center;
  padding: .3rem 0;
}
.footer p{
  color: #b2d5ff;
  font-size: .21rem;
  line-height: .36rem;
  opacity: .5;
}
.share-box{
  display: none;
  position: absolute;
  width: 100%;
  height: 100rem;
  top: 0;
  z-index: 10;
  left: 0;
  opacity: 0;
  letter-spacing: .02rem;
  transition: all .5s ease;
  background: rgba(0, 0, 0, .9);
}
.show{
  display: block;
  opacity: 1;
}
.share-box .close{
  text-align: right;
  padding: .3rem .3rem 1.1rem .3rem;
}
.share-box .close img{
  width: .4rem;
  height: .4rem;
  display: inline-block;
}
.share-box h2{
  color: #fff;
  text-align: center;
  font-size: .36rem;
  height: .52rem;
  line-height: .52rem;
  margin-bottom: .6rem;
  font-weight: normal;
}
.share-box .browser{
  width: 1rem;
  height: 1rem;
  margin-right: .25rem;
}
.share-box .icon{
  width: .6rem;
  height: .6rem;
  margin: 0 .2rem;
}
.share-box .item-list{
  font-size: .3rem;
  color: #b3b3b3;
  padding-left: 1rem;
  word-spacing: -.1rem;
  margin-bottom: .62rem;
}
.share-box .item-list img,.share-box .item-list span{
  vertical-align: middle;
}
@keyframes itemft
{
0% {
    opacity: 0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
}
64% {
    opacity: 0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}
@-webkit-keyframes itemft
{
0% {
    opacity: 0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
}
64% {
    opacity: 0;
    -webkit-transform: translateY(2rem);
            transform: translateY(2rem);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
}



